						alt="" />
					<span>Promoter Strength Prediction</span>
				</div>
				<div
					class="body"
					style="opacity: 0.3">
					<p>Description of the promoter strength pre-diction model:</p>
					<p>
						We developed transcriptional intensity prediction models using a synthetic promoter library with 112,955 sequences. The models include a transformer, a Convolutional Neural Network (CNN),
						and a Long Short-Term Memory (LSTM) framework. Each model's prediction results showed high correlation with experimental data, with coefficients of 0.86, 0.79, and 0.84, respectively.
