						their strength, with the following ranges: >10.5, 3.98-10.5, 1.51-3.98, 0.57-1.51, 0.217-0.57, 0.082-0.217, and &#60;0.031. Using these strength levels, we developed a conditional
						diffusion model to generate novel promoter sequences with target strengths. Experimental validation showed a high correlation of 0.92 between predicted and actual results.
					</p>
					<p>
						Here, we will use a Transformer model to predict the strength of sequences generated by a conditional Diffusion model. Based on the prediction results, we will score the generated novel
